.statistics-container {
    display: grid;
    grid-template-columns: 30% auto;
}

    .statistics-panel h5 {
    text-transform: uppercase;
    margin-bottom: 1rem;
    }
    .statistics-panel table {
    width: 100%;
    display: flex;
    text-align-last: start;
    justify-content: space-evenly;
    }
    .statistics-paneltbody tr td {
        text-transform: uppercase;
    padding: 0.5rem;
    }
    .statistics-panel .label {
        text-transform: uppercase;
    text-align: right;
    font-weight: bold;
    color: gray;
    width: 40%;
    font-size: 0.75em;
    white-space: nowrap;
    }
    .statistics-panel .value {
    text-align: left;
    color: #ffb000;

    font-size: 1.1em;
    -webkit-animation: slideIn 0.5s ease-in-out;
            animation: slideIn 0.5s ease-in-out;
    white-space: nowrap;

    }

  
    .statistics-panel .loading {
    text-align: center;
    font-style: italic;
    color: gray;
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    }
    
    @-webkit-keyframes pulse {
    0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
    50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
    }
    
    @keyframes pulse {
    0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
    50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
    }
    
    @-webkit-keyframes slideIn {
    0% {
    -webkit-transform: translateX(-20%);
            transform: translateX(-20%);
    }
    100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    }
    }
    
    @keyframes slideIn {
    0% {
    -webkit-transform: translateX(-20%);
            transform: translateX(-20%);
    }
    100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    }
    }

.statistics-panel {
    margin: 30px 0;
}

.statistics-panel div {
    margin: 5px 20px;
}

.statistics-panel table th, .statistics-panel table td {
    padding: 5px 20px;
}

.apexcharts-tooltip {
    padding: 10px;
}

.step-10 .apexcharts-xaxis-label,
.step-5 .apexcharts-xaxis-label {
    display: none;
}

.step-10 .apexcharts-xaxis-label:nth-child(10n) {
    display:revert;
}

.step-5 .apexcharts-xaxis-label:nth-child(5n) {
    display:revert;
}

/**********************/
/***** ANIMATIONS ****/
/**********************/

@-webkit-keyframes pulse {
    0% {
      -webkit-transform: scale(1);
