  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  position: relative;
  opacity: 0; /* Initially hidden */
  -webkit-animation: cardAnimation 0.5s ease-in-out forwards;
          animation: cardAnimation 0.5s ease-in-out forwards;
  -webkit-transform-origin: center;
          transform-origin: center;
}

@-webkit-keyframes cardAnimation {
  0% {
    -webkit-transform: translate(100%, -100%) rotateY(180deg);
            transform: translate(100%, -100%) rotateY(180deg); /* Initial position in the top right corner */
    opacity: 0.8;
  }
  90% {
    -webkit-transform: translate(0, 0) rotateY(90deg);
            transform: translate(0, 0) rotateY(90deg); /* Halfway through the animation, the card is halfway flipped */
    opacity: 0.9; /* The card is partially visible */
  }
  100% {
    -webkit-transform: translate(0, 0) rotateY(0);
            transform: translate(0, 0) rotateY(0); /* Final position, fully flipped */
    opacity: 1; /* The card is fully visible */
  }
}

@keyframes cardAnimation {
  0% {
    -webkit-transform: translate(100%, -100%) rotateY(180deg);
            transform: translate(100%, -100%) rotateY(180deg); /* Initial position in the top right corner */
    opacity: 0.8;
